import { useEffect, useState } from 'react';
import Services from '../../../services/Services';
import { v4 as uuidv4 } from 'uuid';
import './TeamForm.css';
import { indexOf } from 'lodash';

function TeamForm({tournamentId, players, teamSize, onSave}) {
  const [team, setTeam] = useState([])

  const select = (index, value) => {
    const teamCopy = [...team];
    if(value){
      teamCopy.push(index);
    } else {
      const indexToRemove = team.indexOf(index);
      if (indexToRemove > -1) {
        teamCopy.splice(indexToRemove, 1);
      }
    }
    const teamCopySliced = teamCopy.slice(Math.max(0, teamCopy.length-teamSize), teamCopy.length);
    setTeam(teamCopySliced);
  }

  useEffect(() => {
    players.forEach((player, index) => {
      if(team.indexOf(index) === -1){
        document.getElementById(player+'_checkbox').checked = false
      }
    });
  }, [team]);

  const save = () => {
    Services.tournaments().saveTeam(tournamentId, {
      id: uuidv4(),
      tournamentId: tournamentId,
      players: team.map((index) => players[index]),
      matches: []
    }).then((team) => {
      onSave(team);
    });
  }

  const playerCheckboxes = players.map((player, index) => {
    return (
      <div key={index} className="form-group h-layout">
        <label htmlFor={player+'_checkbox'} className='flex-grow'>{player}</label>
        <input type='checkbox' id={player+'_checkbox'} checked={team.indexOf(index) > -1} onChange={(e) => select(index, e.target.checked)}/>
      </div>
    )
  });

  return (
    <div className='v-layout align-stretch'>
      <div>
        <h3 className='text-accent'>Players</h3>
        {playerCheckboxes}
      </div>
      <button className='mt' onClick={() => save()}>save</button>
    </div>
  );
}

export default TeamForm;
