import { useState } from 'react';
import Services from '../../../services/Services';
import { v4 as uuidv4 } from 'uuid';
import './MatchForm.css';

function MatchForm({team, onSave}) {
  const [match, setMatch] = useState({
    id: uuidv4(),
    position: '',
    teamScore: team.players.map((player) => {
      return {
        player: player,
        kills: '',
        damage: ''
      }
    })
  })

  const setPlayerKills = (index, value) => {
    const matchCopy = {...match};
    matchCopy.teamScore[index].kills = value;
    setMatch(matchCopy);
  }

  const setPlayerDamage = (index, value) => {
    const matchCopy = {...match};
    matchCopy.teamScore[index].damage = value;
    setMatch(matchCopy);
  }

  const setMatchPosition = (value) => {
    const matchCopy = {...match};
    matchCopy.position = value;
    setMatch(matchCopy);
  }

  const save = () => {
    Services.tournaments().saveMatch(team.tournamentId, team.id, match).then((match) => {
      onSave(match);
    })
  }

  const teamScore = match.teamScore.map((score, index) => {
    return (
      <div key={index} className="form-group match-player-form">
        <label className='flex-grow'>{score.player}</label>
        <input id={'playerNameInput'+index} type='number' value={score.kills}  placeholder='Kills' onChange={(e) => setPlayerKills(index, e.target.value)}/>
        <input id={'playerNameInput'+index} type='number' value={score.damage}  placeholder='Damage' onChange={(e) => setPlayerDamage(index, e.target.value)}/>
      </div>
    )
  });

  return (
    <div className='v-layout align-stretch'>
      <div>
        <div className="form-group h-layout">
          <label htmlFor='matchPositionInput'>Match Position</label>
          <input className='flex-grow' id='matchPositionInput' type='number' value={match.position} onChange={(e) => setMatchPosition(e.target.value)} placeholder='Match position'/>
        </div>
      </div>
      <div>
        <h3 className='flex-grow text-accent'>Players</h3>
        {teamScore}
      </div>
      <button onClick={() => save()}>save</button>
    </div>
  );
}

export default MatchForm;
