import { useState } from 'react';
import Services from '../../../services/Services';
import { v4 as uuidv4 } from 'uuid';
import './TournamentForm.css';

function TournamentForm({onSave}) {
  const [name, setName] = useState('');
  const [teamSize, setTeamSize] = useState(1);
  const [players, setPlayers] = useState(['']);
 
  const addPlayer = () => {
    setPlayers([...players, '']);
  };
 
  const setPlayerName = (index, name) => {
    const copy = [...players];
    copy[index] = name
    setPlayers(copy);
  };
 
  const save = () => {
    Services.tournaments().save({
      id: uuidv4(),
      created: new Date(),
      name: name,
      teamSize: teamSize,
      players: players,
      teams: []
    });

    onSave();
  };

  const playersInput = players.map((player, index) => {
    return (
      <div key={index} className="form-group h-layout">
        <label htmlFor={'playerNameInput'+index}>Player name</label>
        <input className='flex-grow' id={'playerNameInput'+index} type='text' value={player}  placeholder='Player Name' onChange={(e) => setPlayerName(index, e.target.value)}/>
      </div>
    )
  });

  return (
    <div className='mt'>
      <div>
        <div className="form-group h-layout">
          <label htmlFor='tournamentNameInput'>Tournament name</label>
          <input className='flex-grow' id='tournamentNameInput' type='text' value={name} onChange={(e) => setName(e.target.value)} placeholder='Tournament Name'/>
        </div>
        <div className="form-group h-layout">
          <label htmlFor='teamSizeInput'>Team size</label>
          <input className='flex-grow' id='teamSizeInput' type='number' value={teamSize} onChange={(e) => setTeamSize(e.target.value)} placeholder='Team size'/>
        </div>
      </div>
      <div>
        <div className='h-layout justify-left'>
          <h3 className='flex-grow'>Players</h3>
          <button className='btn--secondary' onClick={() => addPlayer()}>+</button>
        </div>
        {playersInput}
      </div>
      <button onClick={() => save()}>save</button>
    </div>
  );
}

export default TournamentForm;
